import React, { useEffect } from "react";
import img1 from "../images/bg.jpg";
import img2 from "../images/clouds_1.png";
import img3 from "../images/clouds_2.png";
import img4 from "../images/man.png";
import img5 from "../images/mountain_left.png";
import img6 from "../images/mountain_right.png";
import { gsap } from "gsap";
import { Typewriter } from "react-simple-typewriter";
import { useNavigate } from "react-router-dom";

const Loading = () => {
  const navigate = useNavigate();
  useEffect(() => {
    gsap.to(".bg", {
      scale: 5,
      duration: 30,
    });
    gsap.to(".man", {
      scale: 0.5,
      duration: 5,
    });
    gsap.to(".mountain_left", {
      x: -300,
      duration: 5,
    });
    gsap.to(".mountain_right", {
      x: 300,
      duration: 5,
    });
    gsap.to(".cloud1", {
      x: 200,
      duration: 5,
    });
    gsap.to(".cloud2", {
      x: -200,
      duration: 5,
    });
    gsap.to(".text", {
      y: 200,
      scale: 1,
      duration: 5,
    });
  });
  setTimeout(() => {
    navigate("/Home");
  }, 5000);
  return (
    <>
      <div className="imagesScroll">
        <img src={img1} alt="" className="bg" />
        <h1 className="text">
          life media<br></br>
          <span>
            <Typewriter
              loop
              cursor="true"
              cursorStyle
              typeSpeed={90}
              deleteSpeed={50}
              delaySpeed={1000}
              words={["Develop", "Design"]}
            />
          </span>
        </h1>
        <img src={img4} alt="" className="man" />
        <img src={img2} alt="" className="cloud1" />
        <img src={img3} alt="" className="cloud2" />
        <img src={img5} alt="" className="mountain_left" />
        <img src={img6} alt="" className="mountain_right" />
      </div>
    </>
  );
};

export default Loading;
